import React from "react";
// import * as RRD from "react-router-dom";
import { About } from "../Components/About";
import { Issues } from "../Components/Issues";
import { Splash } from "../Components/Splash";

export const RouteUrls = {
	Splash: "/",
	About: "/about",
	Issues: "/issues/:issueId",
};

export interface RouteDefinition {
	notExact?: true;
	component?: React.ComponentClass<any>;
	render?: () => React.ReactNode;
	url: string;
}

export const Routes: { [key in keyof typeof RouteUrls]: RouteDefinition } = {
	Splash: { url: RouteUrls.Splash, component: Splash },
	About: { url: RouteUrls.About, component: About },
	Issues: { url: RouteUrls.Issues, component: Issues },
};
